<template>
  <router-view />
</template>

<script>
export default {
  name: "App",
  mounted: function () {
    this.audio = new Audio();
    this.audio.src =
      "https://static.llilii.cn/music/%E3%82%AD%E3%83%9F%E3%83%9A%E3%83%87%E3%82%A3%E3%82%A2.mp3";
    this.audio.loop = "loop";
    this.audio.autoplay = "autoplay";
    let playPromise;
    playPromise = this.audio.play();
    if (playPromise) {
      playPromise
        .then(() => {
          // 音频加载成功
          // 音频的播放需要耗时
          that.tiemr = setInterval(() => {
            second--;
            if (second <= 0) {
              that.audio.pause();
              clearInterval(that.tiemr);
            }
          }, 1000);
        })
        .catch((e) => {
          // 音频加载失败
          //console.error(e);
        });
    }
  },
};
</script>


